const initialState = {
    amount: 0,
};

export default (state = initialState , actions) =>{
    if(actions.type === "DEPOSITE_MONEY"){
        return {
            ...state,
            amount: state.amount + 10,
        }
        
    }
    if(actions.type === "WITHDRAW_MONEY"){
        return {
            ...state,
            amount: state.amount -10,
        }
    } 
    return state;
}