import { deposite , withdraw } from "../Redux/Actions.js"
import {connect} from "react-redux" 

const mapStateToProps = (state ) =>{
    return{
        amount:state.amountReducer.amount,
    }
}

const Counter = ({amount,deposite , withdraw, })=>{
    return(
        <div>
            <h1>Amount{amount}</h1>
            <button onClick={()=> deposite() }> deposite</button>
            <button onClick={()=> withdraw() }> withdraw</button>
        </div>
    )
}

export default connect(mapStateToProps ,{deposite ,withdraw})(Counter)